#hero{
    height: 100vh;
    width: 100%;
    background:linear-gradient(0deg, rgba(31, 31, 31, 0.8), rgba(31, 31, 31, 0.8)), url('/herobg.jpg');
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
#hero #overlay{
    height: 100%;
    width: 100%;
    position: absolute;
    background-color: black;
    opacity: .9;
    z-index: 1;
}
#hero #main{
    height: 100vh;
    width: 100%;
    z-index: 100;
    color: white;
    padding: 7%;
}
#hero #btn-learn{
    width: 70%;
    height: 50px;
    border-radius: 3px;
    border:1px solid #011c34;
    color:white;
    background-color: #034078;
    border-right: 3px solid #011c34;
    border-bottom: 5px solid #011c34;
    cursor: pointer;
    clip-path: polygon(13% 0, 100% 0, 100% 100%, 0% 100%);
    transition: all 1s;
}
#hero #btn-learn:hover{
    background-color: white;
    border-color: #034078;
    font-weight: bolder;
    color: #034078;
    
}
#hero #desc-container{
    border-radius: 3px;
    border: 2px solid #034078;
    padding: 10px;
    font-size: .9rem;
}

@media only screen and (max-width: 900px) {

    #hero #main{
        padding: 15% 7%;
        flex-direction: column;
    }
    #hero #main #profilepic{
        height: 225px !important;
        width: 225px !important;
    }
    #hero #main #profilepic img{
        height: 350px !important;
    }
    #hero .prof-div{
        padding: 7px 0;
    }
    #hero #main h2{
        font-size: 1.1rem;
    }
    #hero #main p{
        font-size: .8rem;
    }
}