#contact{
    padding: 20px 0;
}
#contact .contact-div{
    width: 50%;
}
#contact #emaildiv{
    border-radius: 5px;
    border: 1px solid #034078;
    min-height: 400px;
    width: 100%;
    position: relative;
    padding: 30px 20px;
}
#contact #emaildiv h4{
    position: absolute;
    top: -10px;
    left:20px;
    padding: 0 10px;
    background-color: white;
}
#contact .input-group{
    width: 100%;
    padding: 10px 5%;
    position: relative;
}
#contact .input-group input,
#contact .input-group textarea{
    border: 1px solid #034078;
    border-radius: 5px;
    height: 3.5rem;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
    /* clip-path: polygon(0 0, 91% 0, 100% 100%, 0% 100%); */
    
}
#contact .input-group textarea{
    padding: 10px 5px;
    min-height: 8rem;
    resize: none;
}
#contact .input-group label{
    position: absolute;
    top: -5px;
    left: calc(5% + 5px);
    background-color: white;
    padding: 0 5px;
}
#contact #send-btn{
    color: white;
    background-color: #034078;
    height: 35px;
    width: 120px;
    border:none;
    clip-path: polygon(13% 0, 100% 0, 100% 100%, 0% 100%);
    transition: all 1s;
    cursor: pointer;
    
}
#contact #send-btn:hover{
    color:#034078;
    border: 2px solid #034078;
    background-color: white;
    font-weight: bolder;
}

@media only screen and (max-width: 900px) {
    #contact #main{
        flex-direction: column;
    }
    #contact #main .contact-div{
        width: 100%;
    }
    #contact #emaildiv{
        padding: 30px 5px;
    }
}