#projects{
    border-radius: 3px;
    border: 1px solid #034078;
    min-height: 100px;
    width: 100%;
    position: relative;
}

#projects #header{
    position: absolute;
    top: -15px;
    left: 15px;
    background-color: white;
    padding: 5px 10px;
}

#projects .proj-item{
    border-right: 1px solid #034078;
    border-bottom: 1px solid #034078;
    width: 33.3333%;
    min-height: 400px;
    padding: 20px 15px;
    color: #034078;
}

#projects .proj-img{
    width: 100%;
    height: 200px;
    background-color: #272727;
    border-radius: 5px;
}
#projects .proj-item p{
    font-size: .8rem;
    padding: 10px 0;
}
#projects .proj-item h4{
    padding: 5px 0;
}
@media only screen and (max-width: 900px) {
    #projects #main{
        flex-direction: column;
    }
    #projects #main .proj-item{
        width: 100%;
    }
}