#profile{
    border-radius: 3px;
    overflow: hidden;
    display: flex;
    padding: 20px 0;
}
#profile #exp{
    border-radius: 3px 0 0 3px;
    border: 1px solid #034078;
    border-right: none;
    padding: 10px;
}

#profile #educ{
    border-radius: 0 3px 3px 0;
    border: 1px solid #034078;
    border-left: none;
    padding: 10px;
    background-color: #034078;
    color: white;
}
#profile .circ{
    border-radius: 50%;
    background-color: #0A1128;
    height: 10px;
    width: 10px;
    margin: 5px;
}
#profile .timeline{
    width: 75px;
}
#profile .line{
    width: 2px;
    background-color: #034078;
    min-height: 50px;
    height: 100%;
}
#profile .company{
    font-weight: bolder;
}
#profile .position{
    color: #034078;
    font-size: .8rem;
    font-weight: bolder;
}
#profile .daterange{
    font-size: .7rem;
}
#profile #educ .circ{
    background-color: #fff;
}
#profile #educ .line{
    background-color: #4baac5;
}
#profile #educ .position{
    color: #4baac5;
}

@media only screen and (max-width: 900px) {
    #profile{
        flex-direction: column;
    }
    #profile #exp{
        border-right: 1px solid #0A1128;
        font-size: .8rem;
    }
    #profile #educ{
        font-size: .8rem;
    }
}