#skills{
    clip-path: polygon(0 0, 100% 14%, 100% 100%, 0% 100%);
    width: 100%;
    min-height: 500px;
    background-color: black;
    position: relative;
}

#skills #skill-canvas{
    background-color: black;
    width: 100% !important;
    height: 500px !important;
    animation: ChangeColorLoop 30s infinite;
}

@keyframes ChangeColorLoop{
    0% {background-color: black;}
    25% {background-color: #0A1128;}
    50% {background-color: #034078;}
    75% {background-color: #0e2b46;}
    100% {background-color: black;}
}

#skills .dark-overlay{
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: #272727;
    opacity: .3;
    z-index: 2;
}
#skills #main{
    /* height: 100%; */
    width: 100%;
    top: 20px;
    left: 0;
    position: absolute;
    color:white;
    z-index: 3;
    padding:  5% 7%;
    /* padding-top: 9%; */
}
#skills .skill-group{
    border-radius: 3px;
    border: .5px solid white;
    min-height: 100px;
    width: 100%;
    position: relative;
    background-color: rgba(255, 255, 255, 0.8);
    color: #161616;
    padding: 20px 10px;
}
#skills .skillglabel{
    background-color: #0A1128;
    border: .5px solid white;
    border-radius: 3px;
    position: absolute;;
    top: -10px;
    left: 10px;
    color: white;
    padding: 5px 30px;
    clip-path: polygon(0 0, 91% 0, 100% 100%, 9% 100%);
}
#skills #header{
    color: white;
    position: absolute;
    top: 5%;
    padding: 0 7%;
}

@media only screen and (max-width: 900px) {
    #skills{
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 0% 100%);
    }
    #skills #skill-canvas{
        height: 950px !important;
    }
    #skills #header{
        top: 3% !important;
    }
    #skills #main{
        padding:10% 7% 5% 7%;
    }
    #skills #main .col-6{
        width: 100%;
        padding: 10px 0 !important;
        font-size: .8rem;
    }
}