*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}
.container{
  padding: 20px 7%;
}
.d-flex{
  display: flex;
}
.justify-content-end{
  justify-content: flex-end;
}
.justify-content-center{
  justify-content: center;
}
.align-items-center{
  align-items: center;
}
.flex-row{
  flex-direction: column;
}
.flex-1{
  flex: 1;
}
.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.d-flex .col-6{
  width: 50%;
}

.flex-wrap{
  flex-wrap: wrap;
}